@media screen and (min-width: 992px) {
    .header {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
      padding: 30px;
      background-color: #495f77;
      /*
      background-color: #1C0846;*/
      margin-bottom: 30px;
    }

    .nom {
        color: white;
        font-family: Argesta;
        font-size: 16pt;
    }
  
    nav {
      display: flex;
      flex-direction: row;
      justify-content: right;
      align-items: right;
    }
  
    .menu-left, .menu-right {
        font-family: Argesta;
        font-size: 12pt;
        color: white;
        text-decoration: none;
        padding-left: 30px;
    }
    
    .menu-left:hover, .menu-right:hover {
        font-family: Argesta;
        font-size: 12pt;
        color: white;
        text-decoration: underline;
    }
  
    .menu-left.active, .menu-right.active {
        text-decoration: underline;
    }
  }

